
.card {
  background: black;
  border: 4px rgb(99, 99, 99) solid;
  border-radius: 8px;
  height: 240px;
  margin: 1rem;
  position: relative;
  width: 285px;
  -webkit-box-shadow: 5 5px 6px black (243, 78, 78), 0 3px 6px black (199, 175, 175);
          box-shadow: 5 5px 6px black (243, 78, 78), 0 3px 6px black (199, 175, 175);
  text-align: left;
}

.card > .img-container {
  height: 100%;
  overflow: hidden;
  text-align: center;
  background:#6CADDC;
}

.card > .img-container > img {
  width: 100%;
  height: 100%;
}

/* .card > .content {
  padding-left: 1rem;
  padding-right: 1rem;
  font-size: 15px;
} */

.card > .content > ul {
  list-style-type: none;
}

/* .card > .content > ul > li {
  margin-top: 18px;
} */

.card > .remove {
  color: #ff1744;
  font-size: 48px;
  position: absolute;
  top: -20px;
  right: 5px;
  cursor: pointer;
}

.card > .remove:hover {
  color: #d50000;
}



